import { useLocation, useParams, Outlet, Link, useMatch } from "react-router-dom";
import { Container, Header, Title, Loader } from "../style/CoinsStyle";
import { useQuery } from "@tanstack/react-query";
import { getInfoData, getPriceData } from "../utils/apis/api";
import { Overview, OverviewItem, Description, TabWrap, Tab } from "../style/DetailStyle";
import { Helmet, HelmetProvider } from "react-helmet-async";
interface coinNameType {
    state: {
        name: string;
    };
}

/* API   console   (ex -> temp1  ) 
  key -> Object.keys(temp1).join(); 
  values -> Object.values(temp1).map(v => typeof v).join();  .
*/

interface InfoData {
    id: string;
    name: string;
    symbol: string;
    rank: number;
    is_new: boolean;
    is_active: boolean;
    type: string;
    logo: string;
    description: string;
    message: string;
    open_source: boolean;
    started_at: string;
    development_status: string;
    hardware_wallet: boolean;
    proof_type: string;
    org_structure: string;
    hash_algorithm: string;
    first_data_at: string;
    last_data_at: string;
}

export interface PriceInfo {
    id: string;
    name: string;
    symbol: string;
    rank: number;
    circulating_supply: number;
    total_supply: number;
    max_supply: number;
    beta_value: number;
    first_data_at: string;
    last_updated: string;
    quotes: {
        USD: {
            ath_date: string;
            ath_price: number;
            market_cap: number;
            market_cap_change_24h: number;
            percent_change_1h: number;
            percent_change_1y: number;
            percent_change_6h: number;
            percent_change_7d: number;
            percent_change_12h: number;
            percent_change_15m: number;
            percent_change_24h: number;
            percent_change_30d: number;
            percent_change_30m: number;
            percent_from_price_ath: number;
            price: number;
            volume_24h: number;
            volume_24h_change_24h: number;
        };
    };
}

const Detail = () => {
    // useLocation Link      .
    const { state } = useLocation() as coinNameType;
    const { coinId } = useParams();

    // useMatch  url  object return
    const chartMatch = useMatch("/:coinId/chart");
    const priceMatch = useMatch("/:coinId/price");

    // useQuery        -> 
    // 3  ms  
    const { isLoading: infoLoading, data: infoData } = useQuery<InfoData>(["infoData", coinId], () => getInfoData(coinId));
    const { isLoading: priceLoading, data: priceData } = useQuery<PriceInfo>(["priceData", coinId], () => getPriceData(coinId), {
        // refetchInterval: 5000,
    });
    const loading = infoLoading || priceLoading;

    return (
        <Container>
            <HelmetProvider>
                <Helmet>
                    <title>{state?.name}</title>
                </Helmet>
            </HelmetProvider>
            <Header>
                {/* state name     loading... */}
                <Title>
                    <Link to={"/"}>&lt;</Link>
                    {state?.name}
                </Title>
            </Header>

            {loading ? (
                <Loader>loading...</Loader>
            ) : (
                <>
                    <Overview>
                        <OverviewItem>
                            <span>Rank:</span>
                            <span>{infoData?.rank}</span>
                        </OverviewItem>

                        <OverviewItem>
                            <span>Symbol:</span>
                            <span>${infoData?.symbol}</span>
                        </OverviewItem>

                        <OverviewItem>
                            <span>Price</span>
                            <span>{priceData?.quotes.USD.price.toFixed(3)}</span>
                        </OverviewItem>
                    </Overview>

                    <Description>{infoData?.description}</Description>

                    <Overview>
                        <OverviewItem>
                            <span>Total Suply:</span>
                            <span>{priceData?.total_supply}</span>
                        </OverviewItem>

                        <OverviewItem>
                            <span>Max Supply:</span>
                            <span>{priceData?.max_supply}</span>
                        </OverviewItem>
                    </Overview>

                    <TabWrap>
                        <Tab isActive={chartMatch !== null}>
                            <Link to={`/${coinId}/chart`} state={state}>
                                Chart
                            </Link>
                        </Tab>

                        <Tab isActive={priceMatch !== null}>
                            <Link to={`/${coinId}/price`} state={state}>
                                Price
                            </Link>
                        </Tab>
                    </TabWrap>
                    {/* chart / price & Outlet prop  context    useOutletContext */}
                    <Outlet context={{ coinId, state, priceData }} />
                </>
            )}
        </Container>
    );
};

export default Detail;
